body
{
    background-color: #aaa;
}
.container
{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: -10px;
    padding: 10px;
    flex-direction: column;
}
.head
{
    font-size: 80px;
    color: #202020;
    font-weight: 800;

}
.head span:nth-child(1){
    color: #ff2020;
    text-shadow: 5px, 5px, 5px, rgba(0, 0, 0, 0.8);
}
.head span:nth-child(2){
    color: #ffcc00;
}

@media (max-width: 770px){
    .container {
        margin: 0 10%;
    }
}